body {
  width: 80%;
  height: 80%;
  margin: 2rem auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#one h1 {
  color: darkgreen;
  font-style: italic;
}

#three tr {
  color: rgb(65, 64, 64);
  padding-top: 0.25rem;
  font-size: 1.1rem;
}

#three .item-row td {
  padding-left: 1rem;
}

/* MESSAGES */

#one .error {
  color: crimson;
}
#one .success {
  color: forestgreen;
}
#one .notice {
  background: lightgrey;
  min-width: 50vw;
  max-width: 80%;
  font-size: 1.25rem;
  border-style: solid;
  border-radius: 0.5rem;
  padding: 0.75rem;
  margin-bottom: 0.75rem;
}

/* Portrait */
@media only screen 
  and (min-device-width: 300px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3)
  and (orientation: portrait) { 

  body {
    width: 100%;
    height: 100%;
    margin: 1rem 1rem;
  }

  #three tr {
    color: rgb(65, 64, 64);
    padding-top: 0.25rem;
    font-size: 1rem;
    padding-bottom: 1rem;
    height: 25rem;
    display: flex;
    min-width: 50vw;
    flex-direction: column;
    text-align: center;
  }
  
  #three .item-row td {
    padding-left: 0.25rem;
    display: block;
  }
}
